import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home"
  },
  // {
  //   path: "*",
  //   redirect: "/error"
  // }
];

export default new VueRouter({
  routes,
  mode: "history",
  scrollBehavior(to, from, savedPosition) {
    return window.scrollTo({ top: 0, behavior: "smooth" });
  }
});
