import Node from './node';
import { yearone, yeartwo, yearthree, yearfour, yearfive } from "../mixins/nodes.js";

const { devicePixelRatio = 1, requestAnimationFrame } = window;

let bcr, mouseNode, scrollPos, yearnumber;

export default class NodeGarden {
  constructor (container, year) {
    this.nodes = [];
    this.container = container;
    this.canvas = document.createElement('canvas');
    this.ctx = this.canvas.getContext('2d');
    this.started = false;
    this.nightmode = false;
    this.isMousedown = false;
    this.hover = false;
    this.user = "";
    this.message = "";

    this.force = 1.5;

    // red
    this.color1 = "189, 87, 109";
    // orange
    this.color2 = "204, 121, 49";
    // yellow
    this.color3 = "227, 199, 14";
    // green
    this.color4 = "41, 105, 56";
    // blue
    this.color5 = "127, 190, 212";
    // indigo
    this.color6 = "53, 94, 171";
    // violet
    this.color7 = "212, 150, 224";

    if (devicePixelRatio && (devicePixelRatio !== 1)) {
      // if retina screen, scale canvas
      this.canvas.style.transform = 'scale(' + 1 / devicePixelRatio + ')';
      this.canvas.style.transformOrigin = '0 0';
    }

    this.mouseDown = this.mouseDown.bind(this);
    this.mouseUp = this.mouseUp.bind(this);
    this.mouseLeave = this.mouseLeave.bind(this);

    this.container.addEventListener("mousedown", this.mouseDown);

    this.container.appendChild(this.canvas);
    this.resize(year);

    this.liarNode = {};
    this.container.addEventListener("mousemove", (e) => {
      this.liarNode.x = e.clientX;
      this.liarNode.y = e.clientY;
    });
  }

  mouseDown(e) {
    e.stopPropagation();
    this.isMousedown = true;
    bcr = this.container.getBoundingClientRect();
    scrollPos = {
      x: window.scrollX,
      y: window.scrollY
    };
    // Add mouse node
    mouseNode = new Node(this);
    mouseNode.x = (e.pageX - scrollPos.x - bcr.left) * devicePixelRatio;
    mouseNode.y = (e.pageY - scrollPos.y - bcr.top) * devicePixelRatio;
    mouseNode.m = 20;

    mouseNode.update = () => {};
    mouseNode.reset = () => {};
    mouseNode.render = () => {};

    this.nodes.unshift(mouseNode);

    this.container.addEventListener("mousemove", this.mouseMove);
    this.container.addEventListener("mouseleave", this.mouseLeave);
    this.container.addEventListener("mouseup", this.mouseUp);
  }

  mouseLeave(e) {
    this.container.removeEventListener("mousemove", this.mouseMove);
    this.container.removeEventListener("mouseup", this.mouseUp);
    this.container.removeEventListener("mouseleave", this.mouseLeave);
    for (let i = 0; i < this.nodes.length; i++) {
      if (this.nodes[i] === mouseNode) {
        this.nodes.splice(i--, 1);
        break;
      }
    }
    this.isMousedown = false;
  }

  mouseMove(e) {
    mouseNode.x = (e.pageX - scrollPos.x - bcr.left) * devicePixelRatio;
    mouseNode.y = (e.pageY - scrollPos.y - bcr.top) * devicePixelRatio; 
  }

  mouseUp(e) {
    this.container.removeEventListener("mousemove", this.mouseMove);
    this.container.removeEventListener("mouseup", this.mouseUp);
    for (let i = 0; i < this.nodes.length; i++) {
      if (this.nodes[i] === mouseNode) {
        this.nodes.splice(i--, 1);
        break;
      }
    }
    this.isMousedown = false;
  }

  start (year) {
    if (!this.playing) {
      this.playing = true;
      this.render(year, true);
    }
  }

  stop () {
    if (this.playing) {
      this.playing = false;
    }
  }

  assignYear (year) {
    yearnumber = [];
    if (year === "yearone") {
      yearnumber = yearone;
    } else if (year === "yeartwo") {
      yearnumber = yeartwo;
    } else if (year === "yearthree") {
      yearnumber = yearthree;
    } else if (year === "yearfour") {
      yearnumber = yearfour;
    } else if (year === "yearfive") {
      yearnumber = yearfive;
    }
  }

  resize (year) {
    this.width = this.container.clientWidth * devicePixelRatio;
    this.height = this.container.clientHeight * devicePixelRatio;
    this.area = this.width * this.height;

    this.nodes = [];
    this.assignYear(year);

    // get nodes needed
    this.nodes.length = yearnumber.length;

    // set canvas size
    this.canvas.width = this.width;
    this.canvas.height = this.height;

    let fillGradient = this.ctx.createLinearGradient(0, 0, this.width, 0);
    fillGradient.addColorStop("0", `rgb(${this.color1})`);
    fillGradient.addColorStop("0.14" , `rgb(${this.color2})`);
    fillGradient.addColorStop("0.28", `rgb(${this.color3})`);
    fillGradient.addColorStop("0.42", `rgb(${this.color4})`);
    fillGradient.addColorStop("0.56", `rgb(${this.color5})`);
    fillGradient.addColorStop("0.7", `rgb(${this.color6})`);
    fillGradient.addColorStop("1.0", `rgb(${this.color7})`);

    this.ctx.fillStyle = fillGradient;

    // create nodes
    for (let i = 0; i < this.nodes.length; i++) {
      if (this.nodes[i]) {
        continue;
      }
      this.nodes[i] = new Node(this);
    }
  }

  toggleNightMode (night) {
    let fillGradient = this.ctx.createLinearGradient(0, 0, this.width, 0);
    fillGradient.addColorStop("0", `rgb(${this.color1})`);
    fillGradient.addColorStop("0.14" , `rgb(${this.color2})`);
    fillGradient.addColorStop("0.28", `rgb(${this.color3})`);
    fillGradient.addColorStop("0.42", `rgb(${this.color4})`);
    fillGradient.addColorStop("0.56", `rgb(${this.color5})`);
    fillGradient.addColorStop("0.7", `rgb(${this.color6})`);
    fillGradient.addColorStop("1.0", `rgb(${this.color7})`);

    if (night) {
      this.nightMode = true;
    } else {
      this.nightMode = false;
    }
    if (this.nightMode) {
      document.body.classList.add('nightmode');
    } else {
      document.body.classList.remove('nightmode');
    }
    this.ctx.fillStyle = fillGradient;
  }

  render (year, start, time) {
    if (!this.playing) {
      return;
    }

    if (start) {
      requestAnimationFrame((time) => {
        this.render(year, true, time);
      });
    }

    const deltaTime = time - (this.lastTime || time);

    this.lastTime = time;

    // clear canvas
    this.ctx.clearRect(0, 0, this.width, this.height);

    // update links
    for (let i = 0; i < this.nodes.length - 1; i++) {
      const nodeA = this.nodes[i];
      for (let j = i + 1; j < this.nodes.length; j++) {
        const nodeB = this.nodes[j];
        const squaredDistance = nodeA.squaredDistanceTo(nodeB);

        // calculate gravity force
        // const force = 3 * (nodeA.m * nodeB.m) / squaredDistance;
        const force = this.force * (nodeA.m * nodeB.m) / squaredDistance;

        const opacity = force * 100;

        if (opacity < 0.025) {
          continue;
        }

        if (squaredDistance <= (nodeA.m / 2 + nodeB.m / 2) * (nodeA.m / 2 + nodeB.m / 2)) {
          // collision: remove smaller or equal - never both of them
          if (nodeA.m <= nodeB.m) {
            nodeA.collideTo(nodeB);
          } else {
            nodeB.collideTo(nodeA);
          }
          continue;
        }

        const distance = nodeA.distanceTo(nodeB);

        // calculate gravity direction
        const direction = {
          x: distance.x / distance.total,
          y: distance.y / distance.total
        };

        // draw gravity lines
        this.ctx.beginPath();
        let lineGradient = this.ctx.createLinearGradient(0, 0, this.width, 0);
        lineGradient.addColorStop("0", `rgba(${this.color1},${(opacity < 1 ? opacity : 1)})`);
        lineGradient.addColorStop("0.14" , `rgba(${this.color2},${(opacity < 1 ? opacity : 1)})`);
        lineGradient.addColorStop("0.28", `rgba(${this.color3},${(opacity < 1 ? opacity : 1)})`);
        lineGradient.addColorStop("0.42", `rgba(${this.color4},${(opacity < 1 ? opacity : 1)})`);
        lineGradient.addColorStop("0.56", `rgba(${this.color5},${(opacity < 1 ? opacity : 1)})`);
        lineGradient.addColorStop("0.7", `rgba(${this.color6},${(opacity < 1 ? opacity : 1)})`);
        lineGradient.addColorStop("1.0", `rgba(${this.color7},${(opacity < 1 ? opacity : 1)})`);
        this.ctx.strokeStyle = lineGradient;
        this.ctx.moveTo(nodeA.x, nodeA.y);
        this.ctx.lineTo(nodeB.x, nodeB.y);
        this.ctx.lineWidth = 2;
        this.ctx.stroke();

        nodeA.addForce(force, direction);
        nodeB.addForce(-force, direction);
      }
    }

    this.hover = false;

    this.assignYear(year);

    // render and update nodes
    for (let i = 0; i < this.nodes.length; i++) {
      this.nodes[i].render();
      this.nodes[i].update(deltaTime || 0);
      if (this.isMousedown) {
        continue;
      }
      if (this.nodes[i].distanceTo(this.liarNode).total < this.nodes[i].getDiameter()) {
        this.hover = true;
        this.user = yearnumber[i].name;
        this.message = yearnumber[i].message;
      }
    }
  }
}
