<template>

<div id="app">

  <div id="page-container">

    <!-- Contains node garden serving as background -->
    <div id="background-container" 
      ref="backgroundContainer">
    </div>

    <div id="header-section">
      <div id="mode-container" @click="nightToggle()">
        <div id="mode">
          <font-awesome-icon :icon="['fas', 'moon']" v-if="!night" />
          <font-awesome-icon :icon="['fas', 'sun']" v-else />
        </div>
      </div>
      <div id="year-container">
        <div class="year" :class="{ 'underline': currentYear === 'yearfive' }"
          @click="changeYear('yearfive')">
          '24-'25
        </div>
        <div class="year" :class="{ 'underline': currentYear === 'yearfour' }"
          @click="changeYear('yearfour')">
          '23-'24
        </div>
        <div class="year" :class="{ 'underline': currentYear === 'yearthree' }"
          @click="changeYear('yearthree')">
          '22-'23
        </div>
        <div class="year" :class="{ 'underline': currentYear === 'yeartwo' }"
          @click="changeYear('yeartwo')">
          '21-'22
        </div>
        <div class="year" :class="{ 'underline': currentYear === 'yearone' }"
          @click="changeYear('yearone')">
          '20-'21
        </div>
      </div>
    </div>

    <div v-if="hover"
      id="user-display"
      :class="{ 'fade-in': hover, 'fade-out': fadeOut }">
      <div id="user">{{ user }}</div>
      <div id="message">{{ message }}</div>
    </div>
  
  </div>

</div>

</template>



<script>

// Node garden code
import NodeGarden from './vendors/nodegarden.js';
// Node garden mixins
import garden from "./mixins/garden.js";
export default {
  name: "App",
  mixins: [garden],
  data() {
    return {
      // Contains node garden
      background: {},
      // Keeps track of nodes on the page
      resetNode: 0,
      // Night mode controller
      night: false,
      hover: false,
      user: "",
      message: "",
      hoverTimeout: null,
      fadeTimeout: null,
      fadeOut: false,
      currentYear: "yearfive"
    }
  },
  watch: {
    "background.hover" () {
      if (!this.background.hover) {      
        clearTimeout(this.fadeTimeout);
        clearTimeout(this.hoverTimeout);
        this.fadeTimeout = setTimeout(() => {
          this.fadeOut = true;
        }, 4500);
        this.hoverTimeout = setTimeout(() => {
          this.hover = this.background.hover;
          this.fadeOut = false;
        }, 5000);
      } else {
        clearTimeout(this.fadeTimeout);
        clearTimeout(this.hoverTimeout);
        this.fadeOut = false;
        this.hover = this.background.hover;
      }
    },
    "background.user" () {
      this.user = this.background.user;
    },
    "background.message" () {
      this.message = this.background.message;
    }
  },
  methods: {
    // Controls "night" prop, which controls night mode (checking prop)
    // toggleNightMode() is inside nodegarden.js
    nightToggle() {
      this.night = !this.night;
      this.background.toggleNightMode(this.night);
      this.resizeApp();
    },
    resizeApp() {
      this.setAppHeightAndWidth(this.$refs.backgroundContainer);
    },
    changeYear(year) {
      this.hover = false;
      if (this.currentYear === year) {
        return;
      } else {
        this.currentYear = year;
        this.garden(year);
      }
    },
    garden(year) {
      this.background.stop();
      // let stop register
      setTimeout(() => {
        this.background.start(year);

        if (this.date.getHours() > 18 || this.date.getHours() < 6) {
          this.night = true;
          this.background.toggleNightMode(this.night);
        }

        this.background.resize(this.currentYear);
        this.resizeApp();
      }, 100);
    }
  },
  mounted() {
    // start simulation
    // this.createGarden();
    // Create and start garden
    // createGarden() is inside garden.js
    // start() is inside nodegarden.js
    this.createGarden("background", this.$refs.backgroundContainer, "yearfive");
    this.background.start("yearfive");

    // trigger nightMode automatically
    if (this.date.getHours() > 18 || this.date.getHours() < 6) {
      this.night = true;
      this.background.toggleNightMode(this.night);
    }
    // Listen for window size changes
    window.addEventListener('resize', () => {
      console.log(this.currentYear);
      this.background.resize(this.currentYear);
      this.resizeApp();
    });

    this.resizeApp();
  }
}

</script>



<style lang="scss">

/* See borders while designing pages */
// * {
//   border: 1px dashed red;
// }

* {
  margin: 0;
  padding: 0;
  font-family: "Balsamiq Sans", sans-serif;
  font-size: 100%;
}
*, ::before, ::after {
  box-sizing: border-box;
}

html {
  height: 100%;
  width: 100%;
}

body {
  height: 100%;
  width: 100%;
  background-color: #fffbf5;
  overflow: hidden;
  transition: background-color 0.2s ease-in-out;
}
  body.nightmode {
    background-color: #010020;
  }
  body.nightmode #mode-container {
    color: #fffbf5;
  }
  body.nightmode #mode:hover {
    background-color: #fffbf5;
    color: #010020;
  }
  body.nightmode #user-display {
    color: #fffbf5;
  }
  body.nightmode #year-container {
    color: #fffbf5;
  }

#app {
  height: 100%;
  width: 100%;
}

#page-container {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  overflow-x: hidden;
  overflow-y: auto;
}

#background-container {
  height: 100%;
  width: 100%;
  position: fixed;
  bottom: 0;
  overflow: hidden;
}

#header-section {
  height: 75px;
  // width: 75px;
  width: auto;
  position: fixed;
  top: 0;
  left: 0;
}
  #mode-container {
    height: 75px;
    width: 75px;
    @include flex-center;
    position: fixed;
    top: 0;
    left: 0;
    padding: 1rem;
    border-radius: 50%;
    cursor: pointer;
    z-index: 50;
  }
    #mode {
      height: 45px;
      width: 45px;
      @include flex-center;
      border-radius: 50%;
      transition: background-color 0.2s ease-in-out;
    }
    #mode:hover {
      background-color: #010020;
      color: #fffbf5;
    }
  #year-container {
    height: 75px;
    width: auto;
    @include flex-center;
    position: fixed;
    top: 0;
    left: 75px;
    margin: 0 10px;
    z-index: 50;
  }
    .year {
      cursor: pointer;
    }
    .year {
      margin-right: 25px;
    }
      .year:last-child {
        margin-right: 0;
      }
    .underline {
      text-decoration: underline;
    }

#user-display {
  @include flex-center;
  flex-direction: column;
  height: 100%;
  width: 100%;
  color: #010020;
  user-select: none;
  z-index: -1;
}
  #user-display.fade-in {
    opacity: 0;
    animation-name: "fade-in";
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
  }
  #user-display.fade-out {
    opacity: 100;
    animation-name: "fade-out";
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
  }
  #user {
    font-size: 90px;
  }
  #message {
    width: 50%;
    text-align: center;
    font-size: 20px;
  }

@keyframes fade-in {
  from { opacity: 0; }
  to { opacity: 1; }
}
@keyframes fade-out {
  from { opacity: 1; }
  to { opacity: 0; }
}

@media only screen and (max-width: 1650px) {
  #user {
    font-size: 75px;
  }
  #message {
    font-size: 16px;
  }
}
@media only screen and (max-width: 1350px) {
  #user {
    font-size: 60px;
  }
}
@media only screen and (max-width: 1100px) {
  #user {
    font-size: 50px;
  }
}
@media only screen and (max-width: 950px) {
  #user {
    font-size: 40px;
  }
  #message {
    width: 75%;
    font-size: 14px;
  }
}
@media only screen and (max-width: 775px) {
  #user {
    font-size: 30px;
  }
  #message {
    font-size: 12px;
  }
}
@media only screen and (max-width: 575px) {
  #user {
    font-size: 20px;
  }
  #message {
    width: 80%;
  }
}
@media only screen and (max-width: 400px) {
  #user {
    font-size: 15px;
  }
  #message {
    font-size: 10px;
  }
}

</style>
